* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

nav {
  height: 80px;
  background: #1b1b1b;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 50px 0px 100px;
}

nav .logo {
  font-size: 33px;
  color: #fff;
  font-weight: 600;
}

nav ul {
  display: flex;
  list-style: none;
}

nav ul li {
  margin: 0 5px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
  padding: 8px 10px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

nav ul li a:hover,
nav ul li a.active {
  color: #1b1b1b;
  background: #fff;
}

nav .menu-btn i {
  color: #fff;
  font-size: 30px;
  cursor: pointer;
  display: none;
  width: 10px;
  height: 10px;
}

#click {
  display: none;
}

/* Responsiveness */
@media (max-width: 950px) {
  #click {
    /* display: block; */
    margin-left: auto;
  }

  nav .menu-btn {
    width: 45px;
    height: 45px;
  }

  nav .menu-btn i {
    display: block;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 44px;
  }

  #click:checked~.menu-btn i:before {
    content: "\f00d";
  }

  nav ul {
    position: fixed;
    top: 80px;
    left: -100%;
    background: #111;
    height: 100vh;
    width: 100%;
    display: block;
    text-align: center;
    transition: all 0.3s ease;
    z-index: 10;
  }

  #click:checked~ul {
    left: 0%;
  }

  nav ul li {
    margin: 40px 0;
  }

  nav ul li a {
    font-size: 20px;
    display: block;
  }

  nav ul li a:hover,
  nav ul li a.active {
    color: cyan;
    background: none;
  }
}